<template>
  <div id="app">
    <!-- 导航栏 -->
    <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal">
      <el-menu-item index="1"><el-icon><home /></el-icon> Home</el-menu-item>
      <el-menu-item index="2"><el-icon><info /></el-icon> About</el-menu-item>
      <el-menu-item index="3"><el-icon><phone /></el-icon> Contact</el-menu-item>
    </el-menu>

    <!-- 卡片组件 -->
    <el-card class="box-card">
      <template v-slot:header>
        <span>Welcome to Element Plus</span>
      </template>

      <div>
        <p>This is a simple card component with some description text.</p>
        <el-button type="primary" @click="handleClick">Click Me</el-button>
      </div>
    </el-card>

    <!-- 表单 -->
    <el-card class="box-card">
      <template v-slot:header>
        <span>Sign Up Form</span>
      </template>
      <el-form :model="form" ref="formRef" label-width="100px" class="form">
        <!-- 用户名输入框 -->
        <el-form-item label="Username" prop="username" :rules="[{ required: true, message: 'Please input your username', trigger: 'blur' }]">
          <el-input v-model="form.username" placeholder="Enter your username"></el-input>
        </el-form-item>

        <!-- 密码输入框 -->
        <el-form-item label="Password" prop="password" :rules="[{ required: true, message: 'Please input your password', trigger: 'blur' }]">
          <el-input type="password" v-model="form.password" placeholder="Enter your password"></el-input>
        </el-form-item>

        <!-- 性别选择 -->
        <el-form-item label="Gender" prop="gender" :rules="[{ required: true, message: 'Please select your gender', trigger: 'change' }]">
          <el-radio-group v-model="form.gender">
            <el-radio label="male">Male</el-radio>
            <el-radio label="female">Female</el-radio>
          </el-radio-group>
        </el-form-item>

        <!-- 兴趣爱好选择 -->
        <el-form-item label="Hobbies" prop="hobbies">
          <el-checkbox-group v-model="form.hobbies">
            <el-checkbox label="reading">Reading</el-checkbox>
            <el-checkbox label="traveling">Traveling</el-checkbox>
            <el-checkbox label="coding">Coding</el-checkbox>
          </el-checkbox-group>
        </el-form-item>

        <!-- 提交按钮 -->
        <el-form-item>
          <el-button type="primary" @click="submitForm">Submit</el-button>
        </el-form-item>
      </el-form>
    </el-card>

    <!-- 对话框 -->
    <el-dialog v-model:visible="dialogVisible" title="Hello">
      <span>Welcome to Element Plus!</span>
      <template #footer>
        <el-button @click="dialogVisible = false">Close</el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script>
// 引入 Element Plus 的图标组件
import { Home, Info, Phone } from '@element-plus/icons-vue'

export default {
  data() {
    return {
      // 表单模型数据
      form: {
        username: '',
        password: '',
        gender: '',
        hobbies: []
      },
      dialogVisible: false, // 控制对话框显示/隐藏
      activeIndex: '1', // 当前激活的菜单项
    }
  },
  methods: {
    // 点击按钮时显示对话框
    handleClick() {
      this.dialogVisible = true
    },
    // 提交表单
    submitForm() {
      this.$refs.formRef.validate(valid => {
        if (valid) {
          console.log('Form Submitted:', this.form)
          this.$message.success('Form submitted successfully!')
        } else {
          console.log('Form validation failed')
          return false
        }
      })
    }
  },
  components: {
    Home,
    Info,
    Phone
  }
}
</script>

